
import { defineComponent, ref, onMounted, reactive } from 'vue'
import { useStore } from 'vuex'
import { useRouter, useRoute } from 'vue-router'
import axios from 'axios'
import EasyMDE, { Options } from 'easymde'

import { GlobalDataProps, PostProps, ResponseType, ImageProps } from '@/store'
import ValidateInput, { RulesProp } from '../components/ValidateInput.vue'
import ValidateForm from '../components/ValidateForm.vue'
import Uploader from '../components/Uploader.vue'
import { beforeUploadCheck } from '@/helper'
import createMessage from '@/components/createMessage'
import Edirot from '@/components/Editor.vue'

interface EdirotInstance {
  clear: () => void;
  getMDEInstance: () => EasyMDE | null;
}
export default defineComponent({
  name: 'Login',
  components: {
    ValidateInput,
    ValidateForm,
    Uploader,
    Edirot
  },
  setup () {
    const uploadedData = ref()
    const titleVal = ref('')
    const editorStatus = reactive({
      isValid: true,
      message: ''
    })
    const router = useRouter()
    const route = useRoute()
    const isEditMode = !!route.query.id
    const store = useStore<GlobalDataProps>()
    const textArea = ref<null | HTMLTextAreaElement>(null)
    const editorRef = ref<null | EdirotInstance>()
    let imageId = ''
    const editorOpions: Options = {
      spellChecker: false
    }
    const titleRules: RulesProp = [
      { type: 'required', message: '文章标题不能为空' }
    ]
    const contentVal = ref('')
    const checkEditor = () => {
      if (contentVal.value.trim() === '') {
        editorStatus.isValid = false
        editorStatus.message = '文章详情不能为空'
      } else {
        editorStatus.isValid = true
        editorStatus.message = ''
      }
    }
    onMounted(() => {
      if (editorRef.value) {
        console.log(editorRef.value.getMDEInstance())
      }
      if (isEditMode) {
        store.dispatch('fetchPost', route.query.id).then((rawData: ResponseType<PostProps>) => {
          const currentPost = rawData.data
          if (currentPost.image) {
            uploadedData.value = { data: currentPost.image }
          }
          titleVal.value = currentPost.title
          contentVal.value = currentPost.content || ''
        })
      }
    })

    const handleFileUploaded = (rawData: ResponseType<ImageProps>) => {
      if (rawData.data._id) imageId = rawData.data._id
    }
    const onFormSubmit = (result: boolean) => {
      checkEditor()
      if (result && editorStatus.isValid) {
        const { column, _id } = store.state.user
        if (column) {
          const newPost: PostProps = {
            title: titleVal.value,
            content: contentVal.value,
            column,
            author: _id
          }
          if (imageId) {
            newPost.image = imageId
          }
          const actionName = isEditMode ? 'updatePost' : 'createPost'
          // eslint-disable-next-line multiline-ternary
          const sendData = isEditMode ? {
            id: route.query.id,
            payload: newPost
          } : newPost
          store.dispatch(actionName, sendData).then(() => {
            createMessage('发表成功, 2秒后跳转到文章', 'success', 2000)
            setTimeout(() => {
              router.push({ name: 'column', params: { id: column } })
            }, 2000)
          })
        }
      }
    }
    const handleFileChange = (e: Event) => {
      const target = e.target as HTMLInputElement
      const files = target.files
      if (files) {
        const uploadedFile = files[0]
        const formData = new FormData()
        formData.append(uploadedFile.name, uploadedFile)
        axios.post('/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then((resp: any) => {
          console.log(resp)
        })
      }
    }

    const uploadCheck = (file: File) => {
      const result = beforeUploadCheck(file, { format: ['image/jpeg', 'image/png'], size: 1 })
      const { passed, error } = result
      if (error === 'format') createMessage('上传图片格式只能是 JPG/PNG格式', 'error', 2000)
      if (error === 'size') createMessage('上传图片不能超过 1Mb', 'error', 2000)

      return passed
    }
    return {
      titleRules,
      titleVal,
      contentVal,
      onFormSubmit,
      handleFileChange,
      uploadCheck,
      handleFileUploaded,
      uploadedData,
      isEditMode,
      textArea,
      editorOpions,
      editorRef,
      checkEditor,
      editorStatus
    }
  }
})
